$red: #FF4B32;
$white: #ffffff;
$black:#000000;
$grey:#0C0D0C;
$font-title:'Josefin Sans',
sans-serif;
$font-subtitle:'Open Sans',
sans-serif;
$font-footer:'Poppins',
sans-serif;
* {
    padding: 0;
    margin: 0;
    border: 0;
}

*,
*:before,
*:after {
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
}

:focus,
:active {
    outline: none;
}

a:focus,
a:active {
    outline: none;
}

nav,
footer,
header,
aside {
    display: block;
}

html,
body {
    height: 100%;
    width: 100%;
    font-size: 100%;
    line-height: 1;
    font-size: 14px;
    -ms-text-size-adjust: 100%;
    -moz-text-size-adjust: 100%;
    -webkit-text-size-adjust: 100%;
}

input,
button,
textarea {
    font-family: inherit;
}

input::-ms-clear {
    display: none;
}

button {
    cursor: pointer;
}

button::-moz-focus-inner {
    padding: 0;
    border: 0;
}

a,
a:visited {
    text-decoration: none;
}

a:hover {
    text-decoration: none;
}

ul li {
    list-style: none;
}

img {
    vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-size: inherit;
    font-weight: 400;
}

.bigred {
    font-size: 20px;
    color: red;
}

html,
body {
    width: 100%;
}

@media (max-width:820px) {
    html,
    body {
        width: 80%;
    }
}

@media (max-width:1200px) {
    html,
    body {
        width: 90%;
    }
}

