.button {
    background: $red;
    font-family: $font-subtitle;
    font-size: 16px;
    font-weight: 600;
    line-height: 160%;
    color: $white;
    max-width: 148px;
    width: 100%;
    height: 61px;
    border-radius: 5px;
    margin-top: 48px;
    margin-bottom: 110px;
    animation: infinite button 3s;
}

@keyframes button {
    20%,
    40%,
    60%,
    80%,
    100% {
        opacity: 1;
        transform: scale(1.2, 1.2);
    }
    ;
    10%,
    50%,
    90% {
        opacity: 0.5;
        transform: scale(1, 1);
    }
    30%,
    70% {
        opacity: 0.1;
        transform: scale(1.1, 1.1);
    }
}

.button:hover {
    background: none;
    border: 2px solid $red;
    color: $white;
    transition: color .2s linear;
}

.button:active {
    background: none;
    border: 2px solid $red;
    color: $red;
